html,
body {
  height: 100%;
  background-color: black !important;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
}

main {
  height: 100%;
  width: 100%;
  flex: 1;
}

footer {
  margin-top: auto;
  width: 100%;
}